<template>
  <div class="template-container">
    <!-- 推广计划 -->
    <el-popover ref="popover1" placement="right-start" title="" width="200" trigger="hover" content="推广计划是广告的集合，您可以对同一个推广计划下的多个广告，统一控制限额和投放方式等。">
    </el-popover>
    <div class="template-item-title" v-popover:popover1>推广计划</div>
    <el-tabs v-model="campaignActiveName" @tab-click="handleCampaignClick">
      <el-tab-pane label="新建推广计划" name="newCampaign">
        <el-form ref="campaignForm" :model="form">
          <!--推广目标 begin -->
          <div class="temp-item-block items-list-wrap">
            <div class="items-title">推广目标</div>
            <el-radio-group v-model="product_type" class="temp-radio-group">
              <div class="temp-radio-item" v-for="(val, key)  in productTypeList" :key="key">
                <el-radio :label="key" @change='productTypeChange(val.campaign_type)'>{{val.name}}</el-radio>
                <span class="temp-radio-remark">{{val.desc}}</span>
              </div>
            </el-radio-group>
            <div class="items-title">计划设置</div>
            <div class="temp-item-block">
              <label class="temp-item-label" for="">计划类型：</label>
              <el-radio-group v-model="form.campaign_type" class="radio-inlineblock">
                <el-radio @change='campaignTypeChange(key)' v-for="(val, key, index) in campaignTypeList" :key="key" :label="key">{{val.name}}</el-radio>
              </el-radio-group>
            </div>
            <div class="temp-item-block" v-show='form.campaign_type!="CAMPAIGN_TYPE_WECHAT_MOMENTS"'>
              <label class="temp-item-label" for="">日限额：</label>
              <div class="temp-item-inlineblock">
                <input class="temp-item-inputtext" v-model="form.daily_budget" type="text" value="" />
                <span class="temp-item-remark">元/天</span>
              </div>
            </div>
            <div class="temp-item-block">
              <label class="temp-item-label" for="">投放速度：</label>
              <el-radio-group v-model="form.speed_mode" class="radio-inlineblock">
                <el-radio v-for="(val, key, index) in speedModeList" :key="key" :label="key">{{val}}</el-radio>
              </el-radio-group>
            </div>
            <div class="temp-item-block">
              <label class="temp-item-label" for="">计划名称：</label>
              <div class="temp-item-inlineblock">
                <input class="temp-long-inputtext" @input="campaignNameChange" v-model="form.campaign_name" type="text" value="" maxlength="40" />
                <span class="temp-item-inlayremark">{{ campaign_name_size }}/40</span>
              </div>
            </div>
          </div>
          <!--推广目标 end -->
        </el-form>
      </el-tab-pane>
      <el-tab-pane label="选择已有计划" name="chooseCampaign">
        <el-select v-loading="campaignLoading" class="adCampaignsList" v-model="form.campaign_id" filterable placeholder="请选择" @change="adCampaignsChange">
          <el-option v-for="item in adCampaignsList" :key="item.campaign_id" :label="item.campaign_name" :value="item.campaign_id">
          </el-option>
        </el-select>
      </el-tab-pane>
    </el-tabs>
    <div v-if="step==1">
      <el-button type="primary" @click="stepNext_1">下一步</el-button>
    </div>
    <!-- 广告组 -->
    <el-popover ref="popover2" placement="right-start" title="" width="200" trigger="hover" content="广告组">
    </el-popover>
    <div v-if="step==2 || step==3" class="template-item-title" v-popover:popover2>广告组</div>
    <el-tabs v-if="step==2 || step==3" v-model="adGroupActiveName" @tab-click="handleAdGroupClick">
      <el-tab-pane label="新建广告组" name="newAdGroup">
        <el-form ref="adGroupForm" :model="form" :inline="true">
          <div class="temp-item-block items-list-wrap">
            <div v-if="productShow" class="items-title">目标详情</div>
            <el-form-item v-if="productShow" label="标的物">
              <el-select v-model="form.product_refs_id" filterable placeholder="选择标的物" @change="productChange">
                <el-option v-for="item in productList" :key="item.product_refs_id" :label="item.product_name" :value="item.product_refs_id"></el-option>
              </el-select>
            </el-form-item>
            <!--子标的物详情 begin -->
            <el-form-item label="渠道包" v-show='subProductShow'>
              <el-select v-loading="subProductLoading" v-model="form.sub_product_refs_id" filterable placeholder="选择渠道包">
                <el-option v-for="item in subProductList" :key="item.sub_product_refs_id" :label="item.package_name" :value="item.sub_product_refs_id"></el-option>
              </el-select>
            </el-form-item>
            <!--子标的物详情 end -->
          </div>
          <div v-show='form.campaign_type=="CAMPAIGN_TYPE_WECHAT_MOMENTS"'>
            <label class="temp-item-label" for="">日限额：</label>
            <div class="temp-item-inlineblock">
              <input class="temp-item-inputtext" v-model="form.daily_budget" type="text" value="" />
              <span class="temp-item-remark">元/天</span>
            </div>
          </div>
          <div class="items-title">站点</div>
          <label class="temp-item-label" for="">投放媒体</label>
          <div class="radio-group-inlineblock">
            <el-radio-group v-model="form.site_set">
              <el-radio @change="siteSetChange" class="radio-group-item" v-for="(val,key) in siteSetList" :key="key" :label="key">{{val}}</el-radio>
            </el-radio-group>
          </div>
          <div class="items-title">定向</div>
          <el-select v-model="targetId" filterable remote placeholder="定向ID" @change='changeTargetId' :remote-method="remoteMethod" style="width:200px" :loading="loading">
            <el-option v-for="item in targetList" :key="item.targeting_id" :label="item.targeting_name" :value="item.targeting_id"></el-option>
          </el-select>
          <el-button type="primary" @click="editTarget" :model="target">编辑</el-button>
          <v-target v-if="account_id" :target="target" :readonly="true"></v-target>
          <div class="temp-item-margintop">
            <div class="items-title">排期与出价</div>
            <el-form-item label="活动时间">
              <el-date-picker @change="pickerDateChange" v-model="pickerDate" type="daterange" align="left" unlink-panels range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" :picker-options="pickerOptions2">
              </el-date-picker>
            </el-form-item>
          </div>
          <div>
            <el-form-item label="投放时段">
              <el-radio-group v-model="form.time_range" @change='dateRangeChange' size="medium">
                <el-radio label="0">全天</el-radio>
                <el-radio label="1">自定义</el-radio>
              </el-radio-group>
              <div v-show='timeShow'>
                <el-time-select placeholder="起始时间" v-model="form.start_time" :picker-options="{
                    start: '00:00',
                    step: '01:00',
                    end: '24:00',
                    maxTime: form.end_time
                  }">
                </el-time-select>
                <el-time-select placeholder="结束时间" v-model="form.end_time" :picker-options="{
                    start: '00:00',
                    step: '01:00',
                    end: '24:00',
                    minTime: form.start_time
                  }">
                </el-time-select>
                <a href="javascript:;" onclick="return false">高级模式</a>
              </div>
            </el-form-item>
            <div>
              <el-form-item label="出价方式">
                <el-radio-group v-model="form.billing_event" @change='billingEventChange' size="medium">
                  <el-radio label="BILLINGEVENT_CLICK" border>CPC</el-radio>
                  <el-radio label="BILLINGEVENT_IMPRESSION" border>CPM</el-radio>
                  <el-tooltip class="item" effect="dark" v-bind:content="ocpaMessage" placement="top-start">
                    <el-radio label="BILLINGEVENT_OCPA" border v-bind:disabled="disabledOcpa">oCPA</el-radio>
                  </el-tooltip>
                </el-radio-group>
              </el-form-item>
            </div>
            <div>
              <el-form-item label="优化目标">
                <el-select v-model="form.optimization_goal" filterable placeholder="选择优化目标">
                  <el-option v-for="(val,key) in optimizationGoalList" :key="key" :label="val.name" :value="key"></el-option>
                </el-select>
              </el-form-item>
            </div>
            <div>
              <label class="temp-item-label" for="">出价：</label>
              <div class="temp-item-inlineblock">
                <input class="temp-item-inputtext" v-model="form.bid_amount" type="text" value="" />
                <span class="temp-item-remark">元/{{ amountName }}</span>
                <p class="temp-item-annotation" style="margin-left:20px;">建议出价
                  <span>{{ amountRange }}</span>元/{{ amountName }}</p>
              </div>
            </div>
            <div>
              <label class="temp-item-label" for="">广告组名称：</label>
              <div class="temp-item-inlineblock">
                <input class="temp-long-inputtext" @input="adgroupNameChange" v-model="form.adgroup_name" type="text" value="" maxlength="40" />
                <span class="temp-item-inlayremark">{{ adgroup_name_size }}/40</span>
              </div>
            </div>
          </div>
        </el-form>
      </el-tab-pane>
      <el-tab-pane v-if="campaignActiveName=='chooseCampaign'" label="选择已有广告组" name="chooseAdGroup">
        <el-select v-loading="adGroupLoading" class="adCampaignsList" v-model="form.adgroup_id" filterable placeholder="请选择" @change="adgroupChange">
          <el-option v-for="(item, index)  in adgroupsList" :key="item.adgroup_id" :label="item.adgroup_name" :value="item.adgroup_id">
          </el-option>
        </el-select>
      </el-tab-pane>
    </el-tabs>

    <div v-if="step==2">
      <el-button type="primary" @click="stepNext_2">下一步</el-button>
    </div>

    <!-- 创意 -->
    <el-popover ref="popover3" placement="right-start" title="" width="200" trigger="hover" content="创意">
    </el-popover>
    <div v-if="step==3" class="template-item-title" v-popover:popover3>创意</div>
    <div v-if="step==3" class="temp-item-margintop">
      <el-form ref="adCreativeForm" :model="form">
        <!--落地页 begin -->
        <div class="temp-item-block" v-if="destinationUrlShow">
          <div class="temp-item-title">落地页</div>
          <div class="temp-item-main">
            <el-form-item label="落地页url">
              <input class="temp-long-inputtext" v-model="form.destination_url" type="text" value="" maxlength="1023" />
            </el-form-item>
          </div>
        </div>
        <!--落地页 end -->
        <div class="temp-item-block">
          <div class="temp-item-title">创意规格</div>
          <div class="temp-item-main" style="display:inline-flex">
            <el-table ref="adCreativeTable" v-loading="adCreativeTableLoading" :data="adcreativeList" @current-change="handleCurrentChange" max-height="556" highlight-current-row style="width: 100%" :default-sort="{prop: 'date', order: 'descending'}">
              <el-table-column fixed prop="adcreative_template_id" label="ID" sortable>
              </el-table-column>
              <!-- <el-table-column :render-header="tableHeaderRender" prop="crt_style" label="创意形式" :formatter="formatterCrtstyle">
              </el-table-column> -->
              <el-table-column prop="adcreative_template_name" label="广告版位">
              </el-table-column>
              <el-table-column filter-placement="bottom-start" :filter-method="filterHandler" :filters="element_type_filter" prop="adcreative_template_style" label="创意形式" :formatter="formatterCrtstyle">
              </el-table-column>
              <el-table-column prop="adcreative_template_desc" label="创意规格描述">
              </el-table-column>
            </el-table>
            <div v-if="adcreativeTemplateInfo_row.adcreative_sample_image.image" style="float:right; width:200px;">
              <img style="width:100%" :src="adcreativeTemplateInfo_row.adcreative_sample_image.image" />
              <div class="sample_image_info">
                <p>{{ adcreativeTemplateInfo_row.adcreative_sample_image.name }} <br>
                  <span>(广告可能出现在以上位置)</span>
                </p>
                <span>{{ adcreativeTemplateInfo_row.adcreative_template_size}}</span>
              </div>
            </div>
          </div>
        </div>
        <div class="temp-item-block">
          <div class="temp-item-title">上传创意</div>
          <div class="temp-item-main">
            <el-tabs v-model="tabActiveName" @tab-click="handleTbaClick">
              <el-tab-pane v-for="(tabItem,tabIndex) in tabList" :key="tabItem.name" :label="tabItem.label" :name="tabItem.name">
                <div class="temp-item-block">
                  <label class="temp-item-label" for="">广告名称：</label>
                  <div class="temp-item-inlineblock">
                    <input class="temp-long-inputtext" @input="adNameChange($event,tabItem)" v-model="tabItem.ad_name" type="text" value="" maxlength="40" />
                    <span class="temp-item-inlayremark">{{ tabItem.ad_name_size }}/40</span>
                  </div>
                </div>
                <div class="uploadurl-input">
                  <el-input placeholder="请输入创意名称" name="adcreative_name" v-model="tabItem.adcreative_name" clearable></el-input>
                </div>

                <!-- 结构体 begin -->
                <div v-if="tabItem.elementsData.structList.length > 0 " class="uploadList-wrap" v-for="(uploadItem,index) in tabItem.elementsData.structList" :key="uploadItem.parent.name+index">

                  <div v-for="(childrenList,i) in uploadItem.children">

                    <div v-for="(children,k) in childrenList">
                      <!-- 视频 -->
                      <div class="uploadList-li" v-if="children.element_type=='ELEMENT_TYPE_VIDEO'">
                        <input class="uploadfile-input" @change="uploadfileChange($event,index,tabIndex,'video',k,i)" type="file" />
                        <div class="uploadfile-item">

                          <div>
                            <i class="el-icon-upload icon-upload"></i>
                            <div class="el-upload__text">
                              将视频文件拖到此处，或
                              <em>点击上传</em>
                              <br>
                              <p class="upload-size">
                                {{ children.restriction.video_restriction.min_width || children.restriction.video_restriction.width }}px * {{ children.restriction.video_restriction.min_height || children.restriction.video_restriction.height }}px <br>
                                <span>（推荐尺寸）</span>
                              </p>
                            </div>
                          </div>
                          <div class="el-upload__tip" slot="tip">只能上传{{children.restriction.video_restriction.file_format_text}}文件，且不超过{{ children.restriction.video_restriction.file_size }}kb</div>
                        </div>
                        <el-progress :percentage="children.progressbar.percentage" :status="children.progressbar.status"></el-progress>
                      </div>
                      <!-- 图片 -->
                      <div class="uploadList-li" v-if="children.element_type=='ELEMENT_TYPE_IMAGE'">
                        <input class="uploadfile-input" @change="uploadfileChange($event,index,tabIndex,'image',k,i)" type="file" />
                        <div class="uploadfile-item">
                          <img class="uploadfile-preview" :src="children.preview_url" />
                          <div v-if="!children.preview_url">
                            <i class="el-icon-upload icon-upload"></i>
                            <div class="el-upload__text">
                              将图片文件拖到此处，或
                              <em>点击上传</em>
                              <br>
                              <p class="upload-size">
                                {{ children.restriction.image_restriction.width }}px * {{ children.restriction.image_restriction.height }}px <br>
                                <span>（推荐尺寸）</span>
                              </p>
                            </div>
                          </div>
                          <div class="el-upload__tip" slot="tip">只能上传{{children.restriction.image_restriction.file_format_text}}文件，且不超过{{ children.restriction.image_restriction.file_size }}kb</div>
                        </div>
                        <el-progress :percentage="children.progressbar.percentage" :status="children.progressbar.status"></el-progress>
                      </div>
                      <!-- 动画效果 -->
                      <div v-if="children.element_type=='ELEMENT_TYPE_ENUM'" class="uploadurl-input">
                        <el-select v-model="children.itemvalue" :placeholder="children.description">
                          <el-option v-for="(enumItem,index) in children.enum_property.enumeration" :key="enumItem.value" :value="enumItem.value" :label="enumItem.description"></el-option>
                        </el-select>
                      </div>
                      <!-- 文本 begin -->
                      <div v-if="children.element_type=='ELEMENT_TYPE_TEXT' && children.restriction.text_restriction" class="uploadurl-input">
                        <el-input :minlength="children.restriction.text_restriction.min_length" :maxlength="children.restriction.text_restriction.max_length" :placeholder="children.description" :name="children.name" v-model="children.itemvalue" clearable></el-input>
                      </div>
                      <div v-else-if="children.element_type=='ELEMENT_TYPE_TEXT' && !children.restriction.text_restriction" class="uploadurl-input">
                        <el-input :placeholder="children.description" :name="children.name" v-model="children.itemvalue" clearable></el-input>
                      </div>
                      <!-- URL begin -->
                      <div v-if="children.element_type=='ELEMENT_TYPE_URL'" class="uploadurl-input">
                        <el-input :placeholder="children.description" :name="children.name" v-model="children.itemvalue" clearable></el-input>
                      </div>
                    </div>

                  </div>

                </div>
                <!-- 结构体 begin -->

                <!--  视频 begin -->
                <div v-if="tabItem.elementsData.videoList.length > 0 " class="uploadList-wrap">
                  <div class="uploadList-li" v-for="(uploadItem, index ) in tabItem.elementsData.videoList" :key="uploadItem.name+index">
                    <input class="uploadfile-input" @change="uploadfileChange($event,index,tabIndex,'video')" type="file" />
                    <div class="uploadfile-item">
                      <div>
                        <i class="el-icon-upload icon-upload"></i>
                        <div class="el-upload__text">
                          将视频文件拖到此处，或
                          <em>点击上传</em>
                          <br>
                          <p class="upload-size">
                            {{ uploadItem.restriction.video_restriction.min_width || uploadItem.restriction.video_restriction.width }}px * {{ uploadItem.restriction.video_restriction.min_height || uploadItem.restriction.video_restriction.height }}px <br>
                            <span>（推荐尺寸）</span>
                          </p>
                        </div>
                      </div>
                      <div class="el-upload__tip" slot="tip">只能上传{{uploadItem.restriction.video_restriction.file_format_text}}文件，且不超过{{ uploadItem.restriction.video_restriction.file_size }}kb</div>
                    </div>
                    <el-progress :percentage="uploadItem.progressbar.percentage" :status="uploadItem.progressbar.status"></el-progress>
                  </div>
                </div>
                <!--  视频 end  -->
                <!--  图片 begin -->
                <div v-if="tabItem.elementsData.imageList.length > 0 " class="uploadList-wrap">
                  <div class="uploadList-li" v-for="(uploadItem, index ) in tabItem.elementsData.imageList" :key="uploadItem.name+index">
                    <input class="uploadfile-input" @change="uploadfileChange($event,index,tabIndex,'image')" type="file" />
                    <div class="uploadfile-item">
                      <img class="uploadfile-preview" :src="uploadItem.preview_url" />
                      <div v-if="!uploadItem.preview_url">
                        <i class="el-icon-upload icon-upload"></i>
                        <div class="el-upload__text">
                          将图片文件拖到此处，或
                          <em>点击上传</em>
                          <br>
                          <p class="upload-size">
                            {{ uploadItem.restriction.image_restriction.width }}px * {{ uploadItem.restriction.image_restriction.height }}px <br>
                            <span>（推荐尺寸）</span>
                          </p>
                        </div>
                      </div>
                      <div class="el-upload__tip" slot="tip">只能上传{{uploadItem.restriction.image_restriction.file_format_text}}文件，且不超过{{ uploadItem.restriction.image_restriction.file_size }}kb</div>
                    </div>
                    <el-progress :percentage="uploadItem.progressbar.percentage" :status="uploadItem.progressbar.status"></el-progress>
                  </div>
                </div>
                <!--  图片 end  -->
                <!-- 动画效果 begin -->
                <div v-if="tabItem.elementsData.enumList.length > 0 " v-for="(uploadItem,index) in tabItem.elementsData.enumList" :key="uploadItem.name+index" class="uploadurl-input">
                  <el-select v-model="uploadItem.itemvalue" :placeholder="uploadItem.description">
                    <el-option v-for="(enumItem,index) in uploadItem.enum_property.enumeration" :key="enumItem.value" :value="enumItem.value" :label="enumItem.description"></el-option>
                  </el-select>
                </div>
                <!-- 动画效果 begin -->
                <!-- 文本 begin -->
                <div v-if="tabItem.elementsData.textList.length > 0 " class="uploadurl-input" v-for="(uploadItem,index) in tabItem.elementsData.textList" :key="uploadItem.name+index">
                  <div v-if="uploadItem.element_type=='ELEMENT_TYPE_TEXT' && uploadItem.restriction.text_restriction">
                    <el-input :minlength="uploadItem.restriction.text_restriction.min_length" :maxlength="uploadItem.restriction.text_restriction.max_length" :placeholder="uploadItem.description" :name="uploadItem.name" v-model="uploadItem.itemvalue" clearable></el-input>
                  </div>
                  <div v-else-if="uploadItem.element_type=='ELEMENT_TYPE_TEXT' && !uploadItem.restriction.text_restriction">
                    <el-input :placeholder="uploadItem.description" :name="uploadItem.name" v-model="uploadItem.itemvalue" clearable></el-input>
                  </div>
                </div>
                <!-- 文本 end -->
                <!-- URL begin -->
                <div v-if="tabItem.elementsData.urlList.length > 0 " class="uploadurl-input" v-for="(uploadItem,index) in tabItem.elementsData.urlList" :key="uploadItem.name+index">
                  <el-input :placeholder="uploadItem.description" :name="uploadItem.name" v-model="uploadItem.itemvalue" clearable></el-input>
                </div>
                <!-- URL end -->

              </el-tab-pane>
            </el-tabs>

          </div>
        </div>

      </el-form>

    </div>

    <div v-if="step==3">
      <el-button type="primary" :loading="submitLoading" @click="onSubmit">提交</el-button>
    </div>

    <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" width="50%" :modal="false">
      <v-target :target="target" :locked="true" @change="changeTarget"></v-target>
    </el-dialog>
    <el-dialog title="取消定向包" :visible.sync="dialogConfirmVisible" width="30%" :before-close="handleConfirmClose">
      <span>取消使用定向包 {{target.targeting_name}} ，仅复制该定向包的内容进行编辑</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogConfirmVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogConfirmVisible = false">确 定</el-button>
      </span>
    </el-dialog>

    <div class='estimation'>
      <div class="datas">
        <div class="data-des">
          <h3>预计最大覆盖用户</h3>
          <p>
            <span class="num">{{approximate_count}}</span>&nbsp;人</p>
        </div>
        <div class="data-des">
          <h3>预计最大日曝光量</h3>
          <p>
            <span class="num">{{impression}}</span>&nbsp;次</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import vTarget from '../../components/vTarget'
export default {
  components: {
    'v-target': vTarget
  },
  created() {
    // 获取session
    var arr,
      reg = new RegExp('(^| )PHPSESSID=([^;]*)(;|$)')
    if ((arr = document.cookie.match(reg))) {
      this.uploadHeaders = {
        'VUE-SESSID': unescape(arr[2])
      }
    }
    this.getEnum() // 获取公共枚举
  },
  watch: {
    // 监听 account_id 发生变化
    account_id() {
