      if (this.form.product_refs_id != '') {
        this.ocpaMessage = '检查APP是否达到oCPA使用门槛中...'
        this.checkOcpa()
      } else {
        this.ocpaMessage = '选择标的物，达到门槛方可选择'
        if (this.form.billing_event == 'BILLINGEVENT_OCPA') {
