      var keyList = [
        'site_set',
        'product_type',
        'billing_event',
        'optimization_goal',
        'product_refs_id',
        'time_range',
        'start_time',
        'end_time',
        'time_senior_type',
        'time_series',
        'target',
        'adcreative_template_id'
      ]
      for (var key in keyList) {
        formList[key] = this.form[keyList[key]]
      }
      var form = JSON.stringify(formList)
      return form
    },
    product_refs_id() {
      return this.form.product_refs_id
    },
    element_type_filter() {
      var list = []
      var map = {}
      this.adcreativeList.forEach(element => {
        if (!map.hasOwnProperty(element['adcreative_template_style'])) {
          map[element['adcreative_template_style']] =
            element['adcreative_template_style']
          list.push({
            text: element['adcreative_template_style'],
            value: element['adcreative_template_style']
          })
        }
      })

      return list
    }
  },
  mounted() {
    //console.log(this.listData)
    if (this.targetList.length == 0) {
      this.getTargetList()
    }
  },
  methods: {
    // 创意 ---------------------------------------------------------------------------
    // 广告名称
    adNameChange($event, tabItem) {
      if (tabItem.ad_name) {
        tabItem.ad_name_size = tabItem.ad_name.length
      }
    },
    // 选择创意规格
    handleCurrentChange(currentRow, oldCurrentRow) {
      this.adcreativeTemplateInfo_row = {
        adcreative_sample_image: {}
      }

      if (currentRow) {
        this.form.adcreative_template_id = currentRow.adcreative_template_id
        this.adcreativeTemplateInfo_row = currentRow
        if (this.activeAdcreativeList) {
          this.$confirm('更改创意规格将会清空已上传创意，是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          })
            .then(() => {
              this.defaultCreativity()
              this.uploadElementFiltrate()
            })
            .catch(() => {
              this.defaultCreativity()
            })
        } else {
          this.activeAdcreativeList = true
          this.uploadElementFiltrate()
        }
      } else {
        this.activeAdcreativeList = false
        this.defaultCreativity()
        //this.uploadElementFiltrate()
      }
    },
    // 创意规格过滤
    filterHandler(value, row) {
      return row['adcreative_template_style'] === value
    },
    // 表格数据 格式
    formatterCrtstyle(row, column, cellValue) {
      return row.adcreative_template_size + row.adcreative_template_style
    },
    // 上传创意 -- 上传元素筛选
    uploadElementFiltrate() {
      // console.log('上传创意 -- 上传元素筛选')
      var that = this
      let row = this.adcreativeTemplateInfo_row

      var elements = this.cloneObj(row.adcreative_elements) // 原始数据
      //console.log('row:', elements)

      // 根据数据 归类
      var elementsData = {
        imageList: [], // 图片
        textList: [], // 文本
        videoList: [], // 视频
        urlList: [], // 链接
        enumList: [], // 选择器
        canvasList: [], // 画布
        structList: [], // 结构体
        referenceList: [] //TSA 外部资源引用类型，例如应用宝落地页 id 、视频说说 id 、微信小程序 id
      }
      // 结构体拼装
      var copyElements = []
      for (var i in elements) {
        var item = elements[i]
        if ('ELEMENT_TYPE_STRUCT' == item.element_type) {
          var children = {}
          var structItem = new Object()
          structItem.element_type = 'ELEMENT_TYPE_STRUCT'
          structItem.parent = item
          structItem.name = item.name
          structItem.children = []
          for (var k in elements) {
            if (elements[k]['parent_name'] == item.name) {
              children[elements[k]['name']] = elements[k]
            }
          }
          if (item.array_property && item.array_property.max_number) {
            var max = item.array_property.max_number
            for (var i = 0; i < max; i++) {
              structItem.children.push(children)
            }
          } else {
            structItem.children.push(children)
          }
          // structItem.children = children
          copyElements.push(structItem)
        } else if (item.parent_name == '') {
          copyElements.push(item)
        }
      }

      //  归类
      for (var index in copyElements) {
        var item = {}
        if ('ELEMENT_TYPE_STRUCT' != item.element_type) {
          item = that.dataClassify(copyElements[index])
        }
        //structList
        if ('ELEMENT_TYPE_IMAGE' == item.element_type) {
          elementsData.imageList.push(item)
        } else if ('ELEMENT_TYPE_TEXT' == item.element_type) {
          elementsData.textList.push(item)
        } else if ('ELEMENT_TYPE_VIDEO' == item.element_type) {
          elementsData.videoList.push(item)
        } else if ('ELEMENT_TYPE_URL' == item.element_type) {
          elementsData.urlList.push(item)
        } else if ('ELEMENT_TYPE_ENUM' == item.element_type) {
          elementsData.enumList.push(item)
        } else if ('ELEMENT_TYPE_CANVAS' == item.element_type) {
          elementsData.canvasList.push(item)
        } else if ('ELEMENT_TYPE_REFERENCE' == item.element_type) {
          elementsData.referenceList.push(item)
        } else if ('ELEMENT_TYPE_STRUCT' == item.element_type) {
          var children = copyElements[index]['children']
          var childrenList = []
          for (var i in children) {
            var childrenItem = {}
            for (var z in children[i]) {
              childrenItem[z] = that.dataClassify(children[i][z])
            }
            childrenList.push(childrenItem)
          }
          var obj = {
            parent: that.cloneObj(copyElements[index]['parent']),
            children: childrenList
          }
          elementsData.structList.push(obj)
        }
      }
      this.tabList[0].elementsData = this.cloneObj(elementsData)
      this.tabList[1].elementsData = this.cloneObj(elementsData)
      //console.log('elementsData:', elementsData)
      console.log('this.tabList[0]:', this.tabList[0])
    },

    // 归类
    dataClassify(item) {
      var that = this
      //  console.log('asdfadsf:', item)
      if ('ELEMENT_TYPE_IMAGE' == item.element_type) {
        var text = '',
          file_format_list = item.restriction.image_restriction.file_format
        for (var i in file_format_list) {
          var type = that.imageTypeClassify(file_format_list[i])
          text = text + type + '/'
        }
        text = text.substring(0, text.length - 1)
        item.restriction.image_restriction.file_format_text = text
        item.progressbar = {
          status: '',
          percentage: 0
        }
        item.preview_url = ''
        item.itemvalue = ''
      } else if ('ELEMENT_TYPE_TEXT' == item.element_type) {
        item.itemvalue = ''
      } else if ('ELEMENT_TYPE_VIDEO' == item.element_type) {
        var text = '',
          file_format_list = item.restriction.video_restriction.file_format
        for (var i in file_format_list) {
          var type = that.videoTypeClassify(file_format_list[i])
          text = text + type + '/'
        }
        text = text.substring(0, text.length - 1)
        item.restriction.video_restriction.file_format_text = text
        item.progressbar = {
          status: '',
          percentage: 0
        }
        item.preview_url = ''
        item.itemvalue = ''
      } else if ('ELEMENT_TYPE_URL' == item.element_type) {
        item.itemvalue = ''
      } else if ('ELEMENT_TYPE_ENUM' == item.element_type) {
        item.description = '请选择' + item.description
        item.itemvalue = ''
      } else if ('ELEMENT_TYPE_CANVAS' == item.element_type) {
        //return item
      } else if ('ELEMENT_TYPE_REFERENCE' == item.element_type) {
        //return item
      }
      return item
    },
    //  新增创意 点击事件
    handleTbaClick(tab, event) {
      // 新增创意
      if (tab.name == 'add' && this.activeAdcreativeList) {
        var elementsData = this.cloneObj(this.tabList[tab.index].elementsData)
        this.tabList.splice(tab.index, 0, {
          label: '创意' + (parseInt(tab.index) + 1),
          name: tab.index,
          adcreative_name: '',
          ad_name: '',
          ad_name_size: 0,
          elementsData: elementsData
        })
        this.tabActiveName = tab.index
      } else if (tab.name == 'add') {
        this.$message('请选择创意规格')
        this.tabActiveName = '0'
      }
    },

    // 广告组 ----------------------------------------------------------------------------
    // 获取广告组列表
    getAdgroups(campaign_id) {
      var that = this
      that.adGroupLoading = true
      var params = {
        page: 1,
        page_size: 100,
        adgroup_name: '',
        system_status: '',
        product_type: '',
        adgroup_id: '',
        start_date: '',
        end_date: '',
        campaign_id: campaign_id
      }
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Adgroups/get', {
          params: params
        })
        .then(function(response) {
          that.adGroupLoading = false
          var respData = response.data
          if (respData.code == 0) {
            if (respData.data.list || respData.data.page_info) {
              that.adgroupsList = respData.data.list
            }
          } else {
            that.$message(respData.msg)
          }
        })
        .catch(function(err) {
          that.adGroupLoading = false
          that.$message('获取广告组失败')
        })
    },
    // 选择广告组
    adgroupChange(id) {
      var that = this,
        campaign_type = ''
      for (var i in that.adgroupsList) {
        if (that.adgroupsList[i]['adgroup_id'] == id) {
          // console.log(that.adgroupsList[i]['site_set'][0])
          that.form.site_set = that.adgroupsList[i]['site_set'][0]
          that.siteSetChange(that.form.site_set)
          // console.log('选择广告组：', that.adgroupsList[i]['product_refs_id'])
          that.form.product_refs_id = that.adgroupsList[i]['product_refs_id']
          that.form.sub_product_refs_id = that.adgroupsList[i]['sub_product_refs_id']
          that.productChange(that.adgroupsList[i]['product_refs_id'])
          break
        }
      }
    },
    // 广告组 tab 切换事件
    handleAdGroupClick(tab) {
      if (
        tab.name == 'chooseAdGroup' &&
        this.campaignActiveName == 'chooseCampaign'
      ) {
      } else {
        this.form.adgroup_id = ''
        this.adGroupActiveName = 'newAdGroup'
      }
    },
    // 查询标的物列表 根据推广计划id
    getProducts(product_type) {
      var that = this
      var params = {
        account_id: this.account_id,
        product_type: product_type
      }
      this.$axios
        .get('/Admin/Marketing_Products/list', {
          params: params
        })
        .then(function(response) {
          that.nextLoading = false
          var respData = response.data
          if (respData.code == 0) {
            that.productList = respData.data
          } else {
            that.$message(respData.msg)
          }
        })
        .catch(function(err) {
          that.nextLoading = false
          that.$message('标的物列表获取失败！')
        })
    },
    // 广告组 活动时间 切换
    pickerDateChange() {
      var begin_date = new Date(this.pickerDate[0]),
        end_date = new Date(this.pickerDate[1])
      this.form.begin_date =
        begin_date.getFullYear() +
        '-' +
        (begin_date.getMonth() + 1) +
        '-' +
        begin_date.getDate()
      this.form.end_date =
        end_date.getFullYear() +
        '-' +
        (end_date.getMonth() + 1) +
        '-' +
        end_date.getDate()
    },
    // 投放时段
    dateRangeChange(value) {
      if (value === '1') {
        this.timeShow = true
      } else {
        this.timeShow = false
        this.form.startTime = '00:00'
        this.form.endTime = '24:00'
      }
    },
    // 广告组 选择出价方式
    billingEventChange(value) {
      if (value == 'BILLINGEVENT_IMPRESSION') {
        this.amountName = '千次曝光'
      } else if (value == 'BILLINGEVENT_CLICK') {
        this.amountName = '点击'
      } else {
        this.amountName = '激活'
      }
      this.optimizationGoalList = {}
      for (var key in this.optimizationGoalConfig) {
        if (this.optimizationGoalConfig[key]['billing_event'] == value) {
          this.optimizationGoalList[key] = this.optimizationGoalConfig[key]
        }
      }
      if (
        !this.optimizationGoalList.hasOwnProperty(this.form.optimization_goal)
      ) {
        this.form.optimization_goal = ''
      }
      this.form.bid_amount = ''
    },
    // 广告组名称长度计算
    adgroupNameChange(e) {
      this.adgroup_name_size = this.form.adgroup_name.length
    },
    // 获取创意规格 -- 请求
    getAdcreativetemplateinfo() {
      var that = this
      this.adCreativeTableLoading = true
      var params = {
        product_type: that.form.product_type,
        site_set: that.form.site_set,
        account_id: that.account_id
      }
      this.$axios
        .post('/Admin/Marketing_Adcreativetemplateinfo/get', params)
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            that.adcreativeList = respData.data.list
          } else {
            that.$message(respData.msg)
          }
          that.adCreativeTableLoading = false
        })
        .catch(function(err) {
          that.adCreativeTableLoading = false
          that.$message('网络繁忙，请稍后再试！')
          console.log(err)
        })
    },
    // 设置 投放媒体 -- 站点
    siteSetChange(val) {
      // 判断是否获取创意规格
      if (this.form.product_type != '' && this.form.site_set != '') {
        this.getAdcreativetemplateinfo()
      }
    },
    // 选择标的物 -- 获取落地页（即标的物介绍页面）-- 获取子标的物
    productChange(value) {
      if(this.form.product_type == 'PRODUCT_TYPE_APP_ANDROID_OPEN_PLATFORM'){
        this.subProductShow = true
        this.subProductLoading = true
        this.subProductList = []
        this.getSubProducts({
          product_type: this.form.product_type,
          product_refs_id: value
        })
      }else{
        this.subProductShow = false
        this.form.sub_product_refs_id = ''
      }

      // console.log(value)
      // console.log(this.productList)
      let obj = {}
      obj = this.productList.find(item => {
        return item.product_refs_id === value
      })
      this.form.destination_url = obj.product_url
    },

    // 定向
    //  获取定向列表
    getTargetList(filtering = '') {
      var that = this
      var params = {}

      if (typeof filtering == 'number') {
        params['targeting_id'] = filtering
      } else if (typeof filtering == 'string' && filtering.length > 0) {
        params['filtering'] = filtering
      }
      params['account_id'] = this.account_id
      if (!this.account_id) {
        return
      }
      this.loading = true
      this.$axios
        .get('/Admin/Marketing_Targetings/get', {
          params: params
        })
        .then(function(response) {
          that.loading = false
          var respData = response.data
          if (respData.code == 0) {
            respData.data.list.forEach(element => {
              if (!that.targetIds.hasOwnProperty(element.targeting_id)) {
                that.targetIds[element.targeting_id] = element
                that.targets.push(element)
              }
            })
            if (typeof filtering == 'number') {
              if (
                that.targets.length == 0 ||
                !that.targetIds.hasOwnProperty(filtering)
              ) {
                var el = {
                  targeting_id: filtering,
                  targeting_name: '无效ID:' + filtering
                }
                that.targets.push(el)
                that.targetIds[filtering] = el
              }

              that.targetList = that.targets
              that.target = that.targetIds[filtering]
              that.form.target = that.targetIds[filtering]
              that.targetId = that.target.targeting_id
            } else if (typeof filtering == 'string') {
              that.updateTargetList(filtering)
            }
          } else {
            that.$message(respData.msg)
          }
        })
        .catch(function(err) {
          that.loading = false
          that.$message('请求getTargetList网络繁忙，请稍后再试!')
          console.log(err)
        })
    },
    //  更新定向
    updateTargetList(query) {
      this.targetList = this.targets.filter(item => {
        return (
          item.targeting_name.toLowerCase().indexOf(query.toLowerCase()) > -1
        )
      })
    },
    // 查询
    remoteMethod(query) {
      // if (query !== '') {
      setTimeout(() => {
        this.getTargetList(query)
      }, 200)
      // }
    },
    changeTargetId(itemId) {
      if (itemId) {
        if (this.targetIds.hasOwnProperty(itemId)) {
          this.target = this.targetIds[itemId]
          this.form.target = this.targetIds[itemId]
        }
      }
    },
    changeTarget(item) {
      if (item) {
        if (!this.targetIds.hasOwnProperty(item.targeting_id)) {
          this.targetList.push(item)
          this.targetIds[item.targeting_id] = item
        }
        this.target = item
        this.form.target = item
        this.targetId = item.targeting_id
      }
      this.dialogVisible = false
    },
    handleConfirmClose(done) {
      this.$confirm('确认关闭？')
        .then(_ => {
          done()
        })
        .catch(_ => {})
    },
    editTarget(item) {
      this.dialogTitle = '编辑定向包'
      this.dialogVisible = true
    },

    // 推广计划 --------------------------------------------------------------------------
    // 推广计划 tab 切换事件
    handleCampaignClick(tab) {
      var that = this
      if (tab.name == 'chooseCampaign') {
        that.getAdCampaigns() // 获取计划列表
      } else {
        that.adGroupActiveName = 'newAdGroup'
        that.form.campaign_id = ''
      }
    },
    // 选择已有的推广计划
    adCampaignsChange(id) {
      this.form.adgroup_id = ''
      this.getAdgroups(this.form.campaign_id)
      var that = this
      var list = this.adCampaignsList
      for (var index in list) {
        if (list[index]['campaign_id'] == id) {
          that.form.campaign_type = list[index]['campaign_type']
          that.product_type = list[index]['product_type']
          that.form.product_type = list[index]['product_type']
          var arr = [list[index]['campaign_type']]
          that.productTypeChange(arr)
          that.campaignTypeChange(that.form.campaign_type)
          break
        }
      }
    },

    // 推广目标选择
    productTypeChange(type) {
      if (this.step == 1) {
        this.form.product_type = this.product_type
        //  广告组 -- 标的物是否显示 // (productTypeList)推广目标列表
        this.checkProductType()
        // 判断是否获取创意规格
        if (this.form.product_type != '' && this.form.site_set != '') {
          this.getAdcreativetemplateinfo()
        }
      } else {
        this.$confirm('修改推广目标，已填写的广告内容将会被清空', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(() => {
            this.step = 1
            this.form.product_type = this.product_type
            //  广告组 -- 标的物是否显示 // (productTypeList)推广目标列表
            this.checkProductType()
            // 判断是否获取创意规格
            if (this.form.product_type != '' && this.form.site_set != '') {
              this.getAdcreativetemplateinfo()
            }
          })
          .catch(() => {
            this.product_type = this.form.product_type
            return false
          })
      }
      var that = this
      // 筛选计划类型
      this.campaignTypeList = {}
      var campaign_type = this.form.campaign_type
      this.form.campaign_type = ''
      for (var key in type) {
        if (type[key] == campaign_type) {
          this.form.campaign_type = campaign_type
        }
        this.campaignTypeList[type[key]] = this.campaignTypeConfig[type[key]]
      }
    },
    // 根据推广目标判断是否有标的物参数 (广告组)
    checkProductType() {
      var product_type = this.form.product_type
      var that = this
      if (that.productTypeList[product_type]['product_refs_id'] === 1) {
        that.productShow = true // 广告组 显示标的物
        that.destinationUrlShow = false
        //  查询标的物
        that.getProducts(product_type)
      } else {
        // 不需要选择标的物id
        that.productShow = false
        that.destinationUrlShow = true
        that.form.product_refs_id = ''
        that.form.destination_url = ''
      }
    },
    // 修改 计划类型
    campaignTypeChange(campaign_type) {
      // 站点(广告组投放媒体)
      this.siteSetList = {}
      var campaignSiteSet = this.campaignTypeList[campaign_type]['site_set']
      for (var key in campaignSiteSet) {
        this.siteSetList[campaignSiteSet[key]] = this.siteSetConfig[
          campaignSiteSet[key]
        ]
      }
    },
    // 推广计划名称 过滤字数
    campaignNameChange(e) {
      this.campaign_name_size = this.form.campaign_name.length
    },
    // 获取计划列表
    getAdCampaigns() {
      var that = this
      this.campaignLoading = true
      var params = {
        page: 1,
        page_size: 100,
        campaign_id: '',
        configured_status: '',
        campaign_name: '',
        start_date: '',
        end_date: ''
      }
      params['account_id'] = this.account_id
      this.$axios
        .get('/Admin/Marketing_Campaigns/get', {
          params: params
        })
        .then(function(response) {
