    // 深度拷贝对象
    cloneObj(obj) {
      var str,
        newobj = obj.constructor === Array ? [] : {}
      if (typeof obj !== 'object') {
        return
      } else if (window.JSON) {
        ;(str = JSON.stringify(obj)), //系列化对象
          (newobj = JSON.parse(str)) //还原
      } else {
        for (var i in obj) {
          newobj[i] = typeof obj[i] === 'object' ? cloneObj(obj[i]) : obj[i]
        }
      }
      return newobj
    },
    // 归类 图片格式
    imageTypeClassify(type) {
      var text = ''
      if (type == 'IMAGE_TYPE_GIF') {
        text = 'gif'
      } else if (type == 'IMAGE_TYPE_JPG') {
        text = 'jpg'
      } else if (type == 'IMAGE_TYPE_PNG') {
        text = 'png'
      } else if (type == 'IMAGE_TYPE_SWF') {
        text = 'swf'
      }
      return text
    },

    // 归类 视频格式
    videoTypeClassify(type) {
      var text = ''
      if (type == 'MEDIA_TYPE_MP4') {
        text = 'mp4'
      } else if (type == 'MEDIA_TYPE_AVI') {
        text = 'avi'
      } else if (type == 'MEDIA_TYPE_MOV') {
        text = 'mov'
      } else if (type == 'MEDIA_TYPE_FLV') {
        text = 'flv'
      }
      return text
    },
    /**
     *  单图片和视频上传
     *  @param e
     *  @param tabIndex 创意下标
     *  @param index 元素下标 （结构体：结构体列表下标 ）
     *  @param type 元素类型
     *  @param elementNmae 元素名 （结构体）
     *  @param childrenIndex 子集下标 （结构体）
     */
    uploadfileChange(e, index, tabIndex, type, elementNmae, childrenIndex) {
      // console.log(index, tabIndex)
      var that = this // vm
      var target = e.target || e.currentTarget
      var file = target.files[0],
        tabTarget = {}
      var height, width, file_size, file_format
      // 判断是否为结构体
      if (elementNmae) {
        tabTarget =
          that.tabList[tabIndex]['elementsData'].structList[index].children[
            childrenIndex
          ][elementNmae]
      } else {
        //非结构体
        if (type == 'image') {
          tabTarget = that.tabList[tabIndex]['elementsData'].imageList[index]
        } else {
          tabTarget = that.tabList[tabIndex]['elementsData'].videoList[index]
        }
      }

      if (type == 'image') {
        var restriction = tabTarget.restriction
        ;(height = restriction.image_restriction.height),
          (width = restriction.image_restriction.width),
          (file_size = restriction.image_restriction.file_size),
          (file_format = restriction.image_restriction.file_format)
      } else if (type == 'video') {
        var restriction = tabTarget.restriction
        ;(height =
          restriction.video_restriction.min_height ||
          restriction.video_restriction.height),
          (width =
            restriction.video_restriction.min_width ||
            restriction.video_restriction.width),
          (file_size = restriction.video_restriction.file_size),
          (file_format = restriction.video_restriction.file_format)
      }
      if (!file) {
        // console.log(type + ':' + file)
        return false
      }

      // 格式校验
      if (file.size / 1024 > file_size) {
        this.$message('上传文件不能超过' + file_size + 'kb')
        return false
      }
      if (!this.formatCheck(file_format, file.type, type)) {
        this.$message('上传文件格式不正确')
        return false
      }
      //  根据类型 判断 宽高限制
      if (type == 'image') {
        //读取图片数据
        var reader = new FileReader()
        reader.onload = function(e) {
          var data = e.target.result
          //加载图片获取图片真实宽度和高度
          var image = new Image()
          image.onload = function() {
            var w = image.width
            var h = image.height
            if (width == w && height == h) {
              that.uploadFileAction(
                file,
                index,
                tabIndex,
                type,
                elementNmae,
                childrenIndex
              )
            } else {
              that.$message('上传文件尺寸不正确')
            }
          }
          image.src = data
        }
        reader.readAsDataURL(file)
      } else if (type == 'video') {
        var reader = new FileReader()
        var fileType = file.type
        reader.onload = function(e) {
          var data = e.target.result
          var video = document.createElement('video')
          document.body.appendChild(video)
          video.addEventListener('loadedmetadata', function(e) {
            if (video.videoWidth == width && video.videoHeight == height) {
              that.uploadFileAction(
                file,
                index,
                tabIndex,
                type,
                elementNmae,
                childrenIndex
              )
            } else {
              that.$message('上传文件尺寸不正确')
            }
            video.remove()
          })
          video.setAttribute('src', data)
        }
        reader.readAsDataURL(file)
      }
    },

    /**
     *
     *  @param file
     *  @param tabIndex 创意下标
     *  @param index 元素下标 （结构体：结构体列表下标 ）
     *  @param type 元素类型
     *  @param elementNmae 元素名 （结构体）
     *  @param childrenIndex 子集下标 （结构体）
     */
    uploadFileAction(file, index, tabIndex, type, elementNmae, childrenIndex) {
      var that = this,
        targetList,
        url = ''
      // 判断是否为结构体
      if (elementNmae) {
        targetList =
          that.tabList[tabIndex]['elementsData'].structList[index].children[
            childrenIndex
          ][elementNmae]
      } else {
        //非结构体
        if (type == 'image') {
          targetList = that.tabList[tabIndex]['elementsData'].imageList[index]
        } else {
          targetList = that.tabList[tabIndex]['elementsData'].videoList[index]
        }
      }

      if ('image' == type) {
        url = '/Admin/Marketing_Images/add'
      } else if ('video' == type) {
        url = '/Admin/Marketing_Videos/add'
      }
      var fd = new FormData()
      fd.append('account_id', that.account_id)
      fd.append('file', file)
      if ('video' == type) {
        fd.append('description', file)
      }
      var xhr = new XMLHttpRequest()
      xhr.upload.addEventListener(
        'progress',
        function(evt) {
          //console.log('progressprogressprogressprogressprogress')
          if (evt.lengthComputable) {
            targetList.progressbar.percentage = 60
            Math.round(evt.loaded * 100 / evt.total)
            targetList.progressbar.status = 'success'
          } else {
            if (complete) {
              targetList.progressbar.status = 'success'
            }
          }
        },
        false
      )
      xhr.addEventListener(
        'load',
        function(evt) {
          //console.log('服务器响应：', evt)
        },
        false
      )
      xhr.addEventListener(
        'error',
        function(evt) {
          targetList.progressbar.status = 'exception'
          console.log(evt)
        },
        false
      )
      xhr.addEventListener(
        'abort',
        function() {
          console.log(evt)
        },
        false
      )
      xhr.open('POST', url) //修改成自己的接口
      xhr.setRequestHeader('VUE-SESSID', this.uploadHeaders['VUE-SESSID'])
      xhr.send(fd)
      xhr.onreadystatechange = function() {
        if (xhr.readyState == 4) {
          if (xhr.status == 200) {
            var data = JSON.parse(xhr.response)
            if (data.code == 0) {
              // console.log('onreadystatechange:', data)
              targetList.progressbar.percentage = 100
              targetList.progressbar.status = 'success'
              // console.log(imageList)
              targetList.preview_url = data.data.preview_url
              if ('video' == type) {
                targetList.itemvalue = data.data.video_id
              } else {
                targetList.itemvalue = data.data.image_id
              }
            } else {
              that.$message(data.msg)
              targetList.progressbar.status = 'exception'
            }
          } else {
            targetList.progressbar.status = 'exception'
          }
        }
      }
    },
    // 格式校验
    formatCheck(file_format, type_format, type) {
      //console.log('图片格式校验:', type)
      var text = '',
        that = this
      for (var k in file_format) {
        if (type == 'image') {
          var format = ''
          if (that.imageTypeClassify(file_format[k]) == 'jpg') {
            format = 'jpg|jpeg'
          } else {
            format = that.imageTypeClassify(file_format[k])
          }
          text = text + format + '|'
        } else {
          text = text + that.videoTypeClassify(file_format[k]) + '|'
        }
      }
      text = text.substring(0, text.length - 1)
      //var reg = new RegExp('/(png|jpg|jpeg])$')
      var result = new RegExp('/(' + text + ')$').test(type_format)
      return result
    },
    // 下一步
    stepNext_1() {
      var that = this
      var params = that.form
      console.log(this.form)
      if ('newCampaign' == that.campaignActiveName) {
        if (!params.product_type && params.product_type == '') {
          this.$message.error('请选择推广目标')
          return false
        }
        if (!params.campaign_type && params.campaign_type == '') {
          this.$message.error('请选择计划类型')
          return false
        }
        if (params.campaign_type != 'CAMPAIGN_TYPE_WECHAT_MOMENTS') {
          if (!params.daily_budget && params.daily_budget == '') {
            this.$message.error('请填写日限额')
            return false
          }
        }
        if (!params.speed_mode && params.speed_mode == '') {
          this.$message.error('请选择投放速度')
          return false
        }
        if (!params.campaign_name && params.campaign_name == '') {
          this.$message.error('请填写计划名称')
          return false
        }
        that.step = 2
      } else {
        // 选择已有的
        if (!params.campaign_id && params.campaign_id == '') {
          this.$message.error('请选择推广计划')
          return false
        }
        that.step = 2
      }
    },
    stepNext_2() {
      // console.log(this.form)
      var that = this,
        params = this.form
      if ('newAdGroup' == that.adGroupActiveName) {
        if (!params.site_set && params.site_set == '') {
          this.$message.error('请选择投放媒体')
          return false
        }
        if (params.campaign_type == 'CAMPAIGN_TYPE_WECHAT_MOMENTS') {
          if (!params.daily_budget && params.daily_budget == '') {
            this.$message.error('请填写日限额')
