          }
        }
        that.step = 3
      } else {
        // 选择已有的
        if (!params.adgroup_id && params.adgroup_id == '') {
          this.$message.error('请选择广告组')
          return false
        }
        that.step = 3
      }
    },
    // 确认提交
    onSubmit() {
      var that = this,
        itemList = this.tabList,
        filtration = false,
        filtrationText = '',
        adcreative_elements = [] // 创意列表

      // 筛选校验 数据
      // 遍历创意
      for (var index = 0; index < itemList.length - 1; index++) {
        var elementsData = itemList[index].elementsData //  单个创意元素数据
        var elementsList = new Object() //  创意
        elementsList['adcreative_name'] = itemList[index]['adcreative_name'] // 创意名
        elementsList['ad_name'] = itemList[index]['ad_name'] // 广告名称
        // 判断创意名称
        if (
          !itemList[index]['adcreative_name'] &&
          itemList[index]['adcreative_name'].length > 120
        ) {
          filtration = true
          filtrationText =
            itemList[index]['name'] + '请填写字数在(1-120)的创意名称'
          break
        }
        // console.log('elementsData:', elementsData)
        // 遍历拼装一个创意的 类型 列表
        var typeDataList = [] //
        for (var key in elementsData) {
          if (key == 'structList') {
            for (var k in elementsData[key]) {
              var objList = elementsData[key][k]['children']
              for (var i in objList) {
                for (var name in objList[i]) {
                  typeDataList.push(objList[i][name])
                }
              }
            }
          } else {
            for (var k in elementsData[key]) {
              if (
                'ELEMENT_TYPE_STRUCT' != elementsData[key][k]['element_type']
              ) {
                typeDataList.push(elementsData[key][k])
              }
            }
          }
        }
        // console.log('typeDataList:', typeDataList)
        //  遍历创意的类型列表 --  格式校验
        for (var key in typeDataList) {
          var itemTarget = typeDataList[key]
          var n = index + 1
          // 判断类型
          if (itemTarget.element_type == 'ELEMENT_TYPE_IMAGE') {
            // 图片
            // 判断是否 必填项
            if (itemTarget.required && !itemTarget.itemvalue) {
              filtration = true
              filtrationText = '创意' + n + '请上传图片'
              break
            }
          } else if (itemTarget.element_type == 'ELEMENT_TYPE_VIDEO') {
            // 视频
            // 判断是否 必填项
            if (itemTarget.required && !itemTarget.itemvalue) {
              filtration = true
              filtrationText = '创意' + n + '请上传视频'
              break
            }
          } else if (itemTarget.element_type == 'ELEMENT_TYPE_TEXT') {
            // 文本类型
            // 创意
            if (itemTarget.required && !itemTarget.itemvalue) {
              filtration = true
              filtrationText = '创意' + n + '请填写' + itemTarget.description
              break
            } else if (itemTarget.itemvalue) {
              if (
                itemTarget.restriction &&
                itemTarget.restriction.text_restriction
              ) {
                if (
                  itemTarget.restriction.text_restriction.max_length <
                    itemTarget.itemvalue.length ||
                  itemTarget.restriction.text_restriction.min_length >
                    itemTarget.itemvalue.length
                ) {
                  filtration = true
                  filtrationText =
                    '创意' +
                    n +
                    itemTarget.description +
                    '请输入' +
                    itemTarget.restriction.text_restriction.min_length +
                    '~' +
                    itemTarget.restriction.text_restriction.max_length +
                    '的字符'
                  break
                }
              }
            }
          } else if (itemTarget.element_type == 'ELEMENT_TYPE_URL') {
            // URL
            if (itemTarget.required && !itemTarget.itemvalue) {
              filtration = true
              filtrationText = '创意' + n + itemTarget.description + '不能为空'
              break
            } else if (itemTarget.itemvalue) {
              if (
                itemTarget.restriction &&
                itemTarget.restriction.url_restriction
              ) {
                var url_pattern =
                  itemTarget.restriction.url_restriction.url_pattern
                var reg = new RegExp(url_pattern)
                if (!reg.test(itemTarget.itemvalue)) {
                  filtration = true
                  filtrationText =
                    '创意' + n + itemTarget.description + '格式不正确'
                  break
                }
              }
            }
          } else if (itemTarget.element_type == 'ELEMENT_TYPE_ENUM') {
            // 枚举类型
            if (itemTarget.required && !itemTarget.itemvalue) {
              filtration = true
              filtrationText = '创意' + n + '请选择' + itemTarget.description
              break
            }
          }
        }
        //  格式校验是否通过
        if (filtration) {
          break
        }

        //  创意上传格式 拼装
        for (var key in elementsData) {
          var list = elementsData[key] //
          for (var i in list) {
            //console.log('ii:', list[i])
            if (list[i]['children']) {
              var childrenList = list[i]['children']
              var children = []

              for (var c in childrenList) {
                var obj = {}
                for (var name in childrenList[c]) {
                  obj[name] = childrenList[c][name]['itemvalue']
                }
                children.push(obj)
              }
              if (children.length == 1) {
                elementsList[list[i]['parent']['name']] = children[0]
              } else {
                elementsList[list[i]['parent']['name']] = children
              }
            } else {
              elementsList[list[i]['name']] = list[i]['itemvalue']
            }
          }
        }

        // console.log('单个创意 elementsList :', elementsList)
        //  return false
        // 添加多个创意
        adcreative_elements.push(elementsList)
      }
      //  必填项 校验
      if (filtration) {
        that.$message(filtrationText)
        return false
      }
      // 添加创意列表
      this.form.adcreative_elements = adcreative_elements

      console.log(this.form)
      that.submitLoading = true
      var params = this.form
      params.account_id = that.account_id
      that.$axios
        .post('/Admin/Marketing_Addetail/add', params)
        .then(function(response) {
          var respData = response.data
          that.submitLoading = false
          if (respData.code == 0) {
            that.$message({
              type: 'success',
              message: respData.msg
            })
            setTimeout(function() {
              that.$router.push('/advertising/campaign')
            }, 500)
          } else {
            that.$message.error(respData.msg)
          }
        })
        .catch(function(err) {
          that.submitLoading = false
          that.$message('网络繁忙，请稍后再试！')
          console.log(err)
        })
    }
  },
  data() {
    return {
      submitLoading: false, // 禁止重复提交 -- loading 状态
      // 推广计划 页面  --- --------------------- ------------
      campaignActiveName: 'newCampaign', //  推广计划tab 默认选中值
      campaignLoading: false,
      subProductLoading: false, // 子标的物选择
      productTypeList: {}, // 推广目标 列表
      campaignTypeList: {}, // 推广计划类型
      campaignTypeConfig: {}, // 推广计划类型
      speedModeList: {}, // 推广 投放速度
      adCampaignsList: [], // 推广计划列表
      campaign_name_size: '', //推广计划名称字数
      product_type: '', //推广目标，用于回滚

      // 广告组 页面 ------------------------------------------
      adGroupActiveName: 'newAdGroup', //  广告组 tab 默认选中值
      adGroupLoading: false,
      adgroupsList: [], // 广告组 列表
      productShow: false, // 标的物是否显示
      subProductShow: false, // 子标的物是否显示
      productList: [], // 标的物 列表
      subProductList: [], // 标的物 列表
      siteSetList: {}, // 广告组 站点-投放媒体 [根据计划类型筛选]
      siteSetConfig: {}, // 广告组 站点-投放媒体 [默认值]
      pickerDate: [], // 广告组 活动时间
      pickerOptions2: {
        shortcuts: [
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
              picker.$emit('pick', [start, end])
            }
          }
        ]
      },
      timeShow: false, //  是否显示 自定义时段
      amountName: '点击', // 广告组 出价单位
      ocpaMessage: '选择标的物，达到门槛方可选择',
      disabledOcpa: true, // 出价方式 oCPA 是否显示
      optimizationGoalConfig: {}, // 优化目标 列表
      optimizationGoalList: {}, // 优化目标 列表(根据出价方式筛选的列表)
      amountName: '点击', //  出价单位
      amountRange: '0~0', // 出价单位范围
      approximate_count: 0, //覆盖人数
      impression: 0, // 曝光量
      adgroup_name_size: 0, // 广告组名称长度

      // 创意 -----------------------------------------------
      destinationUrlShow: false, // 是否显示落地页url
      adCreativeTableLoading: false,
      adcreativeList: [], //  创意规格列表
      // 选中创意规格
      adcreativeTemplateInfo_row: {
        adcreative_sample_image: {}
      },
      tabActiveName: '0',
      tabList: [
        {
          label: '创意1',
          name: '0',
          adcreative_name: '',
          elementsData: {
            imageList: [], // 图片
            textList: [], // 文本
            videoList: [], // 视频
            urlList: [], // 链接
            enumList: [], // 选择器
            canvasList: [], // 画布
            referenceList: [] //TSA 外部资源引用类型，例如应用宝落地页 id 、视频说说 id 、微信小程序 id
          }
        },
        {
          label: '+ 添加',
          name: 'add',
          adcreative_name: '',
          elementsData: {
            imageList: [], // 图片
            textList: [], // 文本
            videoList: [], // 视频
            urlList: [], // 链接
            enumList: [], // 选择器
            canvasList: [], // 画布
            referenceList: [] //TSA 外部资源引用类型，例如应用宝落地页 id 、视频说说 id 、微信小程序 id
          }
        }
      ],

      // 定向
      dialogConfirmVisible: false,
      dialogVisible: false,
      loading: false,
      targetIds: {},
      targetId: '',
      targets: [],
      targetList: [],
      target: {
        targeting_id: '',
        targeting_name: ''
      },
      dialogTitle: '',

      //
      step: 1,

      //  页面参数
      form: {
        // 推广计划
        campaign_id: '', // 计划id  (已选择)
        product_type: '', // 推广目标
        campaign_type: '', // 计划类型
        daily_budget: '', // 日限额
        speed_mode: '', // 投放速度
        campaign_name: '', // 投放名称
        // 广告组
        adgroup_id: '', // 广告组id
        product_refs_id: '', // 标的物id
        sub_product_refs_id: '', // 子标的物id
        // daily_budget: '', // 日限额
        site_set: '', // 投放媒体 --站点
        target: {}, // 定向
        begin_date: '',
        begin_date: '',
        time_range: '0', // 投放时段
        start_time: '00:00',
        end_time: '24:00',
        billing_event: '', //出价方式
        optimization_goal: '', // 优化目标
        bid_amount: '', // 出价
        adgroup_name: '', // 广告组名称
        // 创意
        adcreative_template_id: '', // 创意规格id（查询覆盖人数）
        destination_url: '' // 落地页url
      }
    }
  }
}
</script>


<style scoped>
.template-item-title {
  color: #404246;
  font-size: 34px;
  font-weight: 900;
  display: inline-block;
}

.items-title {
  padding: 5px 10px 5px 8px;
  border-left: 3px solid #409eff;
  box-sizing: border-box;
  font-size: 16px;
  background: #f2f6fc;
  color: #555;
  margin-bottom: 20px;
}

.items-list-wrap .temp-radio-item {
  margin-bottom: 15px;
  font-size: 16px;
}

.items-list-wrap .temp-radio-remark {
  display: inline-block;
  color: #999;
  font-size: 14px;
  margin-left: 100px;
}

.radio-inlineblock {
  margin-left: 20px;
}

.adCampaignsList {
  margin-bottom: 20px;
}
.temp-item-margintop {
  margin-top: 20px;
}

.temp-radio-item {
  width: 100%;
  margin: 0 0 20px 0;
}
.temp-radio-remark {
  display: inline-block;
  color: #999;
  font-size: 14px;
  margin-left: 100px;
}

.radio-inlineblock {
  display: inline-block;
  margin-left: 20px;
}

.sample_image_info {
  padding: 20px 0 0 0;
  background: #f8f9fa;
  text-align: center;
}

.sample_image_info p {
  color: #555;
  font-size: 14px;
  text-align: center;
}

.sample_image_info p span {
  color: #999;
  font-size: 12px;
}

.sample_image_info span {
  color: #555;
  font-size: 13px;
  text-align: center;
}

.upload-size {
  font-size: 16px;
  color: #ff5722;
}

.upload-size span {
  font-size: 12px;
  color: #999;
}

.uploadList-wrap {
  width: 100%;
  margin-bottom: 20px;
  float: left;
  position: relative;
  z-index: 2;
}

.upload-li {
  display: inline-block;
}

.uploadList-li {
  float: left;
  width: 360px;
  display: inline-block;
  border: 1px dashed #d9d9d9;
  background: #fff;
  box-sizing: border-box;
  border-radius: 6px;
  text-align: center;
  cursor: pointer;
  position: relative;
  z-index: 2;
  padding-bottom: 10px;
  margin-right: 10px;
}

.upload-demo {
  width: 360px;
  display: inline-block;
  margin-right: 20px;
}

.uploadurl-input {
  width: 600px;
  margin-bottom: 15px;
}

.uploadfile-item {
  position: relative;
  z-index: 3;
}

.uploadfile-item .icon-upload {
  font-size: 57px;
  color: #c0c4cc;
}

.uploadfile-input {
  position: absolute;
  z-index: 5;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}

.uploadfile-preview {
  width: 100%;
}

.estimation {
  width: 200px;
  padding: 15px;
  border: 5px solid transparent;
  background: #f5f6f8;
  border-radius: 9px;
  background-clip: content-box;
  position: fixed;
  top: 100px;
  right: 20px;
  _position: relative;
  z-index: 100;
  _top: 0;
}

.estimation .datas {
  padding: 15px;
}

.estimation .data-des {
  line-height: 24px;
  font-size: 15px;
  color: #555;
  margin-bottom: 26px;
}

.estimation .datas p {
  margin-bottom: 10px;
  line-height: 21px;
}

.estimation .data-des h3 {
  font-size: 13px;
  color: #888;
  margin-bottom: 12px;
  font-family: inherit;
  font-weight: 500;
  line-height: 1.1;
}

.estimation .data-des .num {
  color: #404246;
  font-weight: 700;
  font-size: 19px;
}
</style>



